   -> _ for long naming (* block_name)\n   - (e.g.) header-nav__item--contact\n   - contact(modifer) is set to make itself outstanding\n\nSassを使用している場合は以下のルール\n   ローカル変数を最初に定義します\n   @extendをローカル変数の次に指定します\n   @mixinを@extendの次に指定します\n   @contentを使用している@mixinは最後に指定します\n\nボックスモデルの種類や表示方法を示すプロパティ（box-sizing, display, visibility, floatなど）\n位置情報に関するプロパティ（position, z-indexなど）\nボックスモデルのサイズに関するプロパティ（width, height, margin, padding, borderなど）\nフォント関連のプロパティ（font-size, line-heightなど）\n色に関するプロパティ（color, background-colorなど）\nそれ以外\n\n\n▼ プロパティ宣言順\nmixin\nボックスモデルの種類や表示方法を示すプロパティ（box-sizing, display, visibility, floatなど）\n位置情報に関するプロパティ（position, z-indexなど）\nボックスモデルのサイズに関するプロパティ（width, height, margin, padding, borderなど）\nフォント関連のプロパティ（font-size, line-heightなど）\n色に関するプロパティ（color, background-colorなど）\nそれ以外\n@contentを使用している@mixin\n*/\n\n/*-------------------------------------------------------------------\n|  Foundation                                                       |\n-------------------------------------------------------------------*/\n\n@use \"foundation/vendors\" with (\n   // bootstrap内の変数の上書き\n   $grid-breakpoints: (\n      sp:    320px,\n      tab:   740px,\n      pc:    1024px\n   ),\n   $grid-columns: 24\n);\n@use \"foundation/reset\";\n@use \"foundation/base\";\n\n/*-------------------------------------------------------------------\n|  Layout                                                           |\n|  ワイヤーフレームに定義されるような大きなコンテナブロック(IDセレクタ指定可)   |\n-------------------------------------------------------------------*/\n\n@use \"layout/header\";\n@use \"layout/main\";\n@use \"layout/footer\";\n\n/*-------------------------------------------------------------------\n|  Object                                                           |\n-------------------------------------------------------------------*/\n\n/*  Component  -----------------------------------------*/\n// Component 再利用できるような小さなモジュール、固有の幅や装飾的なスタイルは極力指定しない\n@use \"object/component/button\";\n@use \"object/component/wrapper\";\n\n/*   Project   -----------------------------------------*/\n// プロジェクト固有のUI(殆どのスタイルの追加はこのレイヤー)\n@use \"object/project/auth\";\n@use \"object/project/form\";\n@use \"object/project/modal\";\n@use \"object/project/management\";\n@use \"object/project/shared\";\n\n@use \"object/project/admin/admin\";\n@use \"object/project/admin/user\";\n@use \"object/project/user/entry\";\n@use \"object/project/user/index\";\n\n\n\n\n\n/*    Theme    -----------------------------------------*/\n\n/*   Utility   -----------------------------------------*/\n@use \"object/utility/utility\";","// _entry.scss\n\n/*\n* --- プロパティ宣言順 ------\n* mixin\n* ボックスモデルの種類や表示方法を示すプロパティ（box-sizing, display, visibility, floatなど）\n* 位置情報に関するプロパティ（position, z-indexなど）\n* ボックスモデルのサイズに関するプロパティ（width, height, margin, padding, borderなど）\n* フォント関連のプロパティ（font-size, line-heightなど）\n* 色に関するプロパティ（color, background-colorなど）\n* それ以外\n* @contentを使用している@mixin\n* --- webpack関連 注意事項 ----\n* background-imageは../img~ (import先のstyle.scssで読み込まれるため)\n* \n* --- その他 注意事項 ----\n* HTMLでimgのaltつけ忘れに用心\n* \n*/\n\n@use \"../../../foundation/tools/global\" as *;\n\n// Top セクション -----------------------------------------------\n// #p-entry-top {}\n\n.p-entry-top__entry {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: $space-md;\n  padding: $space-md;\n  border-radius: map-get($border-radius-size, primary);\n}\n\n.p-entry-top__entry-wrap, .p-entry-top__image-wrap { padding: 0 0.5rem; }\n\n.p-entry-top__entry-wrap { margin-bottom: $space-lg; }\n\n.p-entry-top__signin-entry { background-color: map-get($bg-colors, secondary); }\n\n.p-entry-top__signup-entry { background-color: map-get($bg-colors, tertiary); }\n\n\n// About セクション ---------------------------------------------\n// #p-entry-about { }\n\n@import url('https://fonts.googleapis.com/css2?family=Itim&display=swap');\n\n.p-entry-about__news-section-head{\n  margin-bottom: $space-sm;\n  font-size: map-get($font-size, title-primary);\n  font-style: italic;\n}\n\n.p-entry-about__brief-wrap {\n  display: flex;\n  flex-flow: column;\n  justify-content: space-around;\n  text-align: center;\n  margin-bottom: $space-2xlg;\n}\n\n.p-entry-about__github-entry {\n  padding: 5px;\n  border-radius: 0.3rem;\n  font-size: map-get($font-size, link-primary);\n  background-color: map-get($bg-colors, tertiary);\n  @include hover() {\n    color: wheat;\n  }\n}\n\n.p-entry-about__news-list {\n  overflow: scroll;\n  max-height: 65vh;\n  padding: $space-2xsm $space-md;\n  border: 1px solid;\n  border-radius: map-get($border-radius-size, primary);\n  @include mq(sp) {\n    max-height: 55vh;\n  }\n}\n\n.p-entry-about__news-wrap {\n  padding: $space-2xsm 0 $space-xsm;\n  border-bottom: map-get($border, primary-sm);\n}\n\n.p-entry-about__news-wrap:last-child { border: none; }\n\n.p-entry-about__news-header-wrap {\n  display: flex;\n  align-items: center;\n  min-height: 2rem;\n}\n\n.p-entry-about__news-pilar-icon { //? pilar icon componentならん？ (l-headerにあり)\n  border-left: 4px solid white;\n  height: 1.5rem;\n  margin-bottom: auto;\n  margin-right: $space-xsm;\n  margin-top: auto;\n  border-radius: 0.3rem;\n}\n\n.p-entry-about__news-header { font-size: map-get($font-size, title-tertiary); }\n\n.p-entry-about__news-content { line-height: $lineheight-half; }\n\n.p-entry-about__news-date {\n  margin: $space-2xsm 0;\n  font-size: map-get($font-size, date);\n  color: map-get($font-colors, secondary);\n}\n\n\n// Func セクション ----------------------------------------------\n// #p-entry-func {  }\n\n.p-entry-func__modal-entry {\n  margin-bottom: $space-xlg;\n  padding: 0 $space-xsm;\n}\n\n.p-entry-func__modal-entry:nth-child(5) { margin: 0; }\n\n.p-entry-func__img-wrap {\n  transition: scale 0.2s ease;\n  position: relative;\n  padding-top: 66.666667%;\n  margin-bottom: $space-md;\n  border-radius: map-get($border-radius-size, primary);\n  box-shadow: 0.05em 0.15em 0.3em rgba(0, 0, 0, 0.2);\n  @include hover() {\n    scale: 1.05;\n    box-shadow: 0.15em 0.45em 0.9em rgba(0,0,0,0.25);\n  }\n}\n\n.p-entry-func__modal-opener {\n  overflow: hidden;\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  border-radius: map-get($border-radius-size, primary);\n  background-repeat: no-repeat;\n  background-size: cover;\n  background-position: 50% 50%;\n  cursor: pointer;\n}\n\n#p-entry-func__modal-opener--channel { background-image: url(../img/sunflower.jpg); }\n\n#p-entry-func__modal-opener--meeting { background-image: url(../img/space_big.png); }\n\n#p-entry-func__modal-opener--message { background-image: url(../img/icescream.png); }\n\n.p-entry-func__img-txt { text-align: center; }\n\n.p-entry-func__modal-txt {\n  overflow: scroll;\n  max-height: 40em;\n  margin-bottom: $space-md;\n  @include mq(sp) {\n    overflow: unset;\n    max-height: none;\n  }\n}\n\n.p-entry-func__modal-thumnails {\n  text-align: center;\n  >img {\n    width: 75px;\n    height: 75px;\n    margin-right: $space-2xsm;\n    margin-bottom: $space-xsm;\n    border-radius: map-get($border-radius-size, primary);\n    object-fit: cover;\n    cursor: pointer;\n    &[data-status=\"on\"] {\n      scale: 1.1;\n      box-shadow: 0.05em 0.15em 0.3em rgba(0, 0, 0, 0.3);\n    }\n  }\n}\n\n.p-entry-func__modal-mainimage {\n  display: flex;\n  overflow: hidden;\n  >img {\n    max-height: 600px;\n    max-width: 100%;\n  }\n}","// _vendors.scss\n\n/*-------------------------------------------------------------------\n|  Bootstrap                                                        |\n-------------------------------------------------------------------*/\n\n// 関数（カラー、SVG、計算などが操作できるように）\n@import \"~bootstrap/scss/functions\";\n\n// 変数\n@import \"~bootstrap/scss/variables\";\n\n// ツール (map, mixin, grid機能など)\n@import \"~bootstrap/scss/maps\";\n@import \"~bootstrap/scss/mixins\";\n@import \"~bootstrap/scss/grid\";\n\n// Bootstrapから輸入したsassを上書き\n.row, .row > * {\n  padding: 0;\n  margin: 0;\n}","// Row\n//\n// Rows contain your columns.\n\n@if $enable-grid-classes {\n  .row {\n    @include make-row();\n\n    > * {\n      @include make-col-ready();\n    }\n  }\n}\n\n@if $enable-cssgrid {\n  .grid {\n    display: grid;\n    grid-template-rows: repeat(var(--#{$prefix}rows, 1), 1fr);\n    grid-template-columns: repeat(var(--#{$prefix}columns, #{$grid-columns}), 1fr);\n    gap: var(--#{$prefix}gap, #{$grid-gutter-width});\n\n    @include make-cssgrid();\n  }\n}\n\n\n// Columns\n//\n// Common styles for small and large grid columns\n\n@if $enable-grid-classes {\n  @include make-grid-columns();\n}\n","// Grid system\n//\n// Generate semantic grid columns with these mixins.\n\n@mixin make-row($gutter: $grid-gutter-width) {\n  --#{$prefix}gutter-x: #{$gutter};\n  --#{$prefix}gutter-y: 0;\n  display: flex;\n  flex-wrap: wrap;\n  // TODO: Revisit calc order after https://github.com/react-bootstrap/react-bootstrap/issues/6039 is fixed\n  margin-top: calc(-1 * var(--#{$prefix}gutter-y)); // stylelint-disable-line function-disallowed-list\n  margin-right: calc(-.5 * var(--#{$prefix}gutter-x)); // stylelint-disable-line function-disallowed-list\n  margin-left: calc(-.5 * var(--#{$prefix}gutter-x)); // stylelint-disable-line function-disallowed-list\n}\n\n@mixin make-col-ready() {\n  // Add box sizing if only the grid is loaded\n  box-sizing: if(variable-exists(include-column-box-sizing) and $include-column-box-sizing, border-box, null);\n  // Prevent columns from becoming too narrow when at smaller grid tiers by\n  // always setting `width: 100%;`. This works because we set the width\n  // later on to override this initial width.\n  flex-shrink: 0;\n  width: 100%;\n  max-width: 100%; // Prevent `.col-auto`, `.col` (& responsive variants) from breaking out the grid\n  padding-right: calc(var(--#{$prefix}gutter-x) * .5); // stylelint-disable-line function-disallowed-list\n  padding-left: calc(var(--#{$prefix}gutter-x) * .5); // stylelint-disable-line function-disallowed-list\n  margin-top: var(--#{$prefix}gutter-y);\n}\n\n@mixin make-col($size: false, $columns: $grid-columns) {\n  @if $size {\n    flex: 0 0 auto;\n    width: percentage(divide($size, $columns));\n\n  } @else {\n    flex: 1 1 0;\n    max-width: 100%;\n  }\n}\n\n@mixin make-col-auto() {\n  flex: 0 0 auto;\n  width: auto;\n}\n\n@mixin make-col-offset($size, $columns: $grid-columns) {\n  $num: divide($size, $columns);\n  margin-left: if($num == 0, 0, percentage($num));\n}\n\n// Row columns\n//\n// Specify on a parent element(e.g., .row) to force immediate children into NN\n// number of columns. Supports wrapping to new lines, but does not do a Masonry\n// style grid.\n@mixin row-cols($count) {\n  > * {\n    flex: 0 0 auto;\n    width: divide(100%, $count);\n  }\n}\n\n// Framework grid generation\n//\n// Used only by Bootstrap to generate the correct number of grid classes given\n// any value of `$grid-columns`.\n\n@mixin make-grid-columns($columns: $grid-columns, $gutter: $grid-gutter-width, $breakpoints: $grid-breakpoints) {\n  @each $breakpoint in map-keys($breakpoints) {\n    $infix: breakpoint-infix($breakpoint, $breakpoints);\n\n    @include media-breakpoint-up($breakpoint, $breakpoints) {\n      // Provide basic `.col-{bp}` classes for equal-width flexbox columns\n      .col#{$infix} {\n        flex: 1 0 0%; // Flexbugs #4: https://github.com/philipwalton/flexbugs#flexbug-4\n      }\n\n      .row-cols#{$infix}-auto > * {\n        @include make-col-auto();\n      }\n\n      @if $grid-row-columns > 0 {\n        @for $i from 1 through $grid-row-columns {\n          .row-cols#{$infix}-#{$i} {\n            @include row-cols($i);\n          }\n        }\n      }\n\n      .col#{$infix}-auto {\n        @include make-col-auto();\n      }\n\n      @if $columns > 0 {\n        @for $i from 1 through $columns {\n          .col#{$infix}-#{$i} {\n            @include make-col($i, $columns);\n          }\n        }\n\n        // `$columns - 1` because offsetting by the width of an entire row isn't possible\n        @for $i from 0 through ($columns - 1) {\n          @if not ($infix == \"\" and $i == 0) { // Avoid emitting useless .offset-0\n            .offset#{$infix}-#{$i} {\n              @include make-col-offset($i, $columns);\n            }\n          }\n        }\n      }\n\n      // Gutters\n      //\n      // Make use of `.g-*`, `.gx-*` or `.gy-*` utilities to change spacing between the columns.\n      @each $key, $value in $gutters {\n        .g#{$infix}-#{$key},\n        .gx#{$infix}-#{$key} {\n          --#{$prefix}gutter-x: #{$value};\n        }\n\n        .g#{$infix}-#{$key},\n        .gy#{$infix}-#{$key} {\n          --#{$prefix}gutter-y: #{$value};\n        }\n      }\n    }\n  }\n}\n\n@mixin make-cssgrid($columns: $grid-columns, $breakpoints: $grid-breakpoints) {\n  @each $breakpoint in map-keys($breakpoints) {\n    $infix: breakpoint-infix($breakpoint, $breakpoints);\n\n    @include media-breakpoint-up($breakpoint, $breakpoints) {\n      @if $columns > 0 {\n        @for $i from 1 through $columns {\n          .g-col#{$infix}-#{$i} {\n            grid-column: auto / span $i;\n          }\n        }\n\n        // Start with `1` because `0` is and invalid value.\n        // Ends with `$columns - 1` because offsetting by the width of an entire row isn't possible.\n        @for $i from 1 through ($columns - 1) {\n          .g-start#{$infix}-#{$i} {\n            grid-column-start: $i;\n          }\n        }\n      }\n    }\n  }\n}\n","// Breakpoint viewport sizes and media queries.\n//\n// Breakpoints are defined as a map of (name: minimum width), order from small to large:\n//\n//    (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px)\n//\n// The map defined in the `$grid-breakpoints` global variable is used as the `$breakpoints` argument by default.\n\n// Name of the next breakpoint, or null for the last breakpoint.\n//\n//    >> breakpoint-next(sm)\n//    md\n//    >> breakpoint-next(sm, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))\n//    md\n//    >> breakpoint-next(sm, $breakpoint-names: (xs sm md lg xl xxl))\n//    md\n@function breakpoint-next($name, $breakpoints: $grid-breakpoints, $breakpoint-names: map-keys($breakpoints)) {\n  $n: index($breakpoint-names, $name);\n  @if not $n {\n    @error \"breakpoint `#{$name}` not found in `#{$breakpoints}`\";\n  }\n  @return if($n < length($breakpoint-names), nth($breakpoint-names, $n + 1), null);\n}\n\n// Minimum breakpoint width. Null for the smallest (first) breakpoint.\n//\n//    >> breakpoint-min(sm, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))\n//    576px\n@function breakpoint-min($name, $breakpoints: $grid-breakpoints) {\n  $min: map-get($breakpoints, $name);\n  @return if($min != 0, $min, null);\n}\n\n// Maximum breakpoint width.\n// The maximum value is reduced by 0.02px to work around the limitations of\n// `min-` and `max-` prefixes and viewports with fractional widths.\n// See https://www.w3.org/TR/mediaqueries-4/#mq-min-max\n// Uses 0.02px rather than 0.01px to work around a current rounding bug in Safari.\n// See https://bugs.webkit.org/show_bug.cgi?id=178261\n//\n//    >> breakpoint-max(md, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))\n//    767.98px\n@function breakpoint-max($name, $breakpoints: $grid-breakpoints) {\n  $max: map-get($breakpoints, $name);\n  @return if($max and $max > 0, $max - .02, null);\n}\n\n// Returns a blank string if smallest breakpoint, otherwise returns the name with a dash in front.\n// Useful for making responsive utilities.\n//\n//    >> breakpoint-infix(xs, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))\n//    \"\"  (Returns a blank string)\n//    >> breakpoint-infix(sm, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))\n//    \"-sm\"\n@function breakpoint-infix($name, $breakpoints: $grid-breakpoints) {\n  @return if(breakpoint-min($name, $breakpoints) == null, \"\", \"-#{$name}\");\n}\n\n// Media of at least the minimum breakpoint width. No query for the smallest breakpoint.\n// Makes the @content apply to the given breakpoint and wider.\n@mixin media-breakpoint-up($name, $breakpoints: $grid-breakpoints) {\n  $min: breakpoint-min($name, $breakpoints);\n  @if $min {\n    @media (min-width: $min) {\n      @content;\n    }\n  } @else {\n    @content;\n  }\n}\n\n// Media of at most the maximum breakpoint width. No query for the largest breakpoint.\n// Makes the @content apply to the given breakpoint and narrower.\n@mixin media-breakpoint-down($name, $breakpoints: $grid-breakpoints) {\n  $max: breakpoint-max($name, $breakpoints);\n  @if $max {\n    @media (max-width: $max) {\n      @content;\n    }\n  } @else {\n    @content;\n  }\n}\n\n// Media that spans multiple breakpoint widths.\n// Makes the @content apply between the min and max breakpoints\n@mixin media-breakpoint-between($lower, $upper, $breakpoints: $grid-breakpoints) {\n  $min: breakpoint-min($lower, $breakpoints);\n  $max: breakpoint-max($upper, $breakpoints);\n\n  @if $min != null and $max != null {\n    @media (min-width: $min) and (max-width: $max) {\n      @content;\n    }\n  } @else if $max == null {\n    @include media-breakpoint-up($lower, $breakpoints) {\n      @content;\n    }\n  } @else if $min == null {\n    @include media-breakpoint-down($upper, $breakpoints) {\n      @content;\n    }\n  }\n}\n\n// Media between the breakpoint's minimum and maximum widths.\n// No minimum for the smallest breakpoint, and no maximum for the largest one.\n// Makes the @content apply only to the given breakpoint, not viewports any wider or narrower.\n@mixin media-breakpoint-only($name, $breakpoints: $grid-breakpoints) {\n  $min:  breakpoint-min($name, $breakpoints);\n  $next: breakpoint-next($name, $breakpoints);\n  $max:  breakpoint-max($next, $breakpoints);\n\n  @if $min != null and $max != null {\n    @media (min-width: $min) and (max-width: $max) {\n      @content;\n    }\n  } @else if $max == null {\n    @include media-breakpoint-up($name, $breakpoints) {\n      @content;\n    }\n  } @else if $min == null {\n    @include media-breakpoint-down($next, $breakpoints) {\n      @content;\n    }\n  }\n}\n","/* http://meyerweb.com/eric/tools/css/reset/ \n  v2.0 | 20110126\n  License: none (public domain)\n*/\n\nhtml, body, div, span, applet, object, iframe,\nh1, h2, h3, h4, h5, h6, p, blockquote, pre,\na, abbr, acronym, address, big, cite, code,\ndel, dfn, em, img, ins, kbd, q, s, samp,\nsmall, strike, strong, sub, sup, tt, var,\nb, u, i, center,\ndl, dt, dd, ol, ul, li,\nfieldset, form, label, legend,\ntable, caption, tbody, tfoot, thead, tr, th, td,\narticle, aside, canvas, details, embed, \nfigure, figcaption, footer, header, hgroup, \nmenu, nav, output, ruby, section, summary,\ntime, mark, audio, video {\n  margin: 0;\n  padding: 0;\n  border: 0;\n  font: inherit;\n  vertical-align: baseline;\n}\n/* HTML5 display-role reset for older browsers */\narticle, aside, details, figcaption, figure, \nfooter, header, hgroup, menu, nav, section {\n\tdisplay: block;\n}\n// body {\n// \tline-height: 1;\n// }\nol, ul {\n  list-style: none;\n}\nblockquote, q {\n  quotes: none;\n}\nblockquote:before, blockquote:after,\nq:before, q:after {\n  content: '';\n  content: none;\n}\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n}","/** #Index ... 各項目に # を付けて検索できます\n  ▼ Bootstrapにて使用される変数の上書き\n * MediaQuery..........メディアクエリ時に使用する変数\n\n  ▼ 自作の変数\n * Color...............色の変数 (font, bg, btn, border)\n * Font................fontの変数(size, family)\n * Line height.........line-heightのサイズを定める変数\n * Border..............borderのサイズを定める変数\n * Spacing.............要素の外/内側に設ける空間を定める変数(margin, padding)\n * Layout..............Layout配下の要素で、操作を受けやすいプロパティ値を変数化\n * Project.............Project配下の要素で、操作を受けやすいプロパティ値を変数化\n * \n * \n * \n * \n * \n * \n * \n**/\n\n\n/*-------------------------------------\n|   #Bootstrapにて使用される変数の上書き   |\n-------------------------------------*/\n\n/*  #MediaQuery  ---------------------------------------------*/\n\n// bootstrapのgrid機能にて使用\n$grid-breakpoints: (\n  sp:    480px,\n  tab:   768px,\n  pc:    1024px\n);\n\n/*  使用方法   //! 要記載\nhtmlでのクラス記載...\n\nrow\nrow-cols-sp-1\nrow-cols-tab-2\nrow-cols-pc-4\n\ncol\n*/\n\n\n\n\n/*---------------\n|   #自作の変数   |\n---------------*/\n\n/*  #Color  ---------------------------------------------*/\n\n// 各色の変数\n$blue:       #0d6efd !default;\n$pink:       #d63384 !default;\n$red:        #dc3545 !default;\n$orange:     #fd7e14 !default;\n$yellow:     #ffc107 !default;\n$green:      #198754 !default;\n$teal:       #20c997 !default;\n$cyan:       #0dcaf0 !default;\n$white:      #fff !default;\n$black:      #1b1b1b!default;\n\n$gray-2xl:   #888 !default;\n$gray-xl:    #777 !default;\n$gray-l:     #666 !default;\n$gray-md:    #555 !default;\n$gray-h:     #444 !default;\n$gray-xh:    #333 !default;\n$gray-2xh:   #222 !default;\n\n// フォントの色\n$font-colors: (\n  \"primary\":       $white,\n  \"secondary\":     $gray-xl,\n  \"white-back\":    $black,\n  \"info\":          $red,\n  \"alert\":         $red,\n  \"danger\":        $red,\n  \"light\":         $red,\n  \"dark\":          $red,\n) !default;\n\n// 背景の色\n$bg-colors: (\n  \"primary\":        $black,\n  \"secondary\":      $gray-2xh,\n  \"tertiary\":       $gray-xh,\n  \"quaternary\":     $gray-h,\n  \"quinary\":        $gray-md,\n  \"active\":         $gray-h,\n  \"loading\":        $white,\n  \"modal\":          $gray-h\n) !default;\n\n// ボタンの色\n$btn-bg-colors: (\n  \"primary\":        $gray-xh,\n  \"secondary\":      $gray-l,\n  \"white-back\":     $black,\n) !default;\n\n$border-colors: (\n  \"primary\":       $gray-xl,\n  \"secondary\":     $gray-2xl,\n  \"white-back\":    $black,\n) !default;\n\n\n/*  #Font  ---------------------------------------------*/\n$font-size-base:           1rem !default;\n$font-size-2xsm:           $font-size-base * 0.62 !default;\n$font-size-xsm:            $font-size-base * 0.72 !default;\n$font-size-sm:             