r-size-lg:        $border-size-base * 2 !default;\n$border-size-xlg:        $border-size-base * 4 !default;\n\n\n$border: (\n  \"primary\":            $border-size-md solid map-get($border-colors, primary),\n  \"primary-sm\":         $border-size-sm solid map-get($border-colors, primary),\n  \"secondary\":          $border-size-md solid map-get($border-colors, secondary),\n  \"secondary-sm\":       $border-size-sm solid map-get($border-colors, secondary),\n  \"white-back\":         $border-size-md solid map-get($border-colors, white-back),\n  \"notice-card\":        $border-size-xlg solid map-get($border-colors, primary),\n) !default;\n\n\n$border-radius-base:    1rem !default;\n$border-radius-sm:      $border-radius-base * 0.3 !default;\n$border-radius-md:      $border-radius-base;\n$border-radius-lg:      $border-radius-base * 2 !default;\n\n$prsnt-border-radius-base:    10% !default;\n$prsnt-border-radius-sm:      $border-radius-base * 0.3 !default;\n$prsnt-border-radius-md:      $border-radius-base;\n$prsnt-border-radius-lg:      $border-radius-base * 2 !default;\n$prsnt-border-radius-full:    $border-radius-base * 5 !default;\n\n$border-radius-size: (\n  \"primary\":            $border-radius-sm,\n  \"secondary\":          $border-radius-md,\n  \"tertiary\":           $border-radius-lg,\n  \"p-primary\":          $prsnt-border-radius-sm,\n  \"p-secondary\":        $prsnt-border-radius-md,\n  \"full\":               $prsnt-border-radius-full,\n\n) !default;\n\n\n\n/*  #Space (padding / margin)  ---------------------------------------------*/\n$space-base:          1rem !default;\n$space-2xsm:          $space-base * 0.3 !default;\n$space-xsm:           $space-base * 0.5 !default;\n$space-sm:            $space-base * 0.7 !default;\n$space-md:            $space-base * 1 !default;\n$space-lg:            $space-base * 1.5 !default;\n$space-xlg:           $space-base * 2 !default;\n$space-2xlg:          $space-base * 3 !default;\n$space-3xlg:          $space-base * 4 !default;\n$space-auto:          auto !default;\n\n\n/*  #Layout  -----------------------------------------------------------------*/\n// Header\n$header-height:          60px !default;\n$l-header: (\n  \"height\":              $header-height,\n) !default;\n\n// Main\n$main-content-width:          85% !default;\n$main-content-maxwidth:       1000px !default;\n$l-main: (\n  \"content-width\": $main-content-width,\n  \"content-maxwidth\": $main-content-maxwidth,\n) !default;\n\n// Footer\n$l-footer: (\n  \"height\":                   60px,\n  \"padding\":                  0 $space-2xlg,\n) !default;\n\n/*  #Project   ---------------------------------------------------------------*/\n// modal\n$p-modal-height-base:     100% !default;\n$p-modal-maxheight:       $p-modal-height-base * 0.88 !default;\n$p-modal-top-pos:         ($p-modal-height-base - $p-modal-maxheight) / 2;\n$p-modal: (\n  \"bg-color\":                 map-get($bg-colors, entry-modal),\n  \"modal-maxheight\":          $p-modal-maxheight,\n  \"modal-top-pos\":            $p-modal-top-pos,\n) !default;\n\n// entry\n$p-entry: (\n  \"header-font-size\":         $font-size-2xlg,\n  \"txt-font-size\":            $font-size-sm,\n) !default;\n\n$p-entry-top: (\n\n) !default;\n\n$p-entry-about: (\n  \"bg-color\":                 $gray-md,\n) !default;\n\n// index\n$p-index-sidebar: (\n  \"width\":                 245px,\n) !default;","/*--------\n|  Base  | - default/reset\n--------*/\n\n@use 'tools/global' as *;\n\n* {\n  box-sizing: border-box;\n  color: inherit;\n}\n\n// html, body {\n//   height: 100%;\n// }\n\nbody {\n  margin: auto;\n  color: map-get($font-colors, primary);\n  background-color: $black;\n  font-size: map-get($font-size, primary);\n  font-family: \"Helvetica Neue\", \"Helvetica\", Helvetica, Arial, sans-serif;\n  -webkit-font-smoothing: antialiased; \n  -webkit-text-size-adjust: none;\n}\n\na {\n  outline: none;\n  text-decoration: none;\n  color: inherit;\n  cursor: pointer;\n}\n\np { overflow-wrap: break-word; }\n\nbutton, input {\n  padding: 0;\n  border: none;\n  outline: none;\n  font: inherit;\n}\n\nbutton {\n  background-color: inherit;\n  cursor: pointer;\n}\n\nlabel {\n  cursor: pointer;\n}","// _header.scss\n\n@use \"../foundation/tools/global\" as *;\n\n.l-header__submenu-toggler,\n.l-header__main-layer-entry, .l-header__sub-layer-entry {\n  display: flex;\n  align-items: center;\n  height: 46px;\n  cursor: pointer;\n  @include hover() {\n    background-color: map-get($bg-colors, tertiary);\n  }\n  @include mq(pc) {\n    padding: 0 $space-sm;\n    border-radius: map-get($border-radius-size, primary);\n  }\n}\n\n.l-header__sub-layer-entry { \n  display: flex;\n  align-items: center;\n}\n\n#l-header {\n  align-items: center;\n  z-index: 5;\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: map-get($l-header, height);\n  background-color: map-get($bg-colors, secondary);\n}\n\n/* --------------------------------------------------------------------\n|  height, background-colorは                                          |\n|  l-header-wrapの裏からスライド表示されるl-header-menuの透過を隠す目的       |\n----------------------------------------------------------------------*/\n.l-header__wrap {\n  display: flex;\n  flex: 1;\n  flex-flow: row;\n  align-items: center;\n  z-index: 4;\n  height: 100%;\n  width: 100%;\n  padding: 0 $space-lg;\n  background-color: map-get($bg-colors, secondary);\n  @include mq(tab-max) {\n    flex: none;\n  }\n}\n\n\n/* --------------------------------------------------------------------\n|  left menu                                                           |\n----------------------------------------------------------------------*/\n\n\n.l-header__leftmenu {\n  display: flex;\n  margin-right: auto;\n  font-size: map-get($font-size, link-primary);\n}\n\n.l-header__home-entry, .l-header__home-entry--arrow,\n#l-header__server-banner, #l-header__channel-banner {\n  display: flex;\n  align-items: center;\n  height: 45px;\n  padding: 0 $space-sm;\n  border-radius: map-get($border-radius-size, primary);\n  @include hover() {\n    background-color: map-get($bg-colors, tertiary);\n  }\n}\n\n#l-header__server-banner, #l-header__channel-banner {\n  @include mq(sp) {\n    padding: 0 $space-xsm;\n  }\n}\n\n.l-header__home-entry--arrow {\n  position: relative;\n  display: inline-block;\n  &::before{\n    content: '';\n    position: absolute;\n    top: 57.5%;\n    right: 0.3rem;\n    transform: rotate(225deg);\n    width: 7px;\n    height: 7px;\n    margin-top: -0.5rem;\n    border-right: solid 1.5px map-get($font-colors, primary);\n    border-top: solid 1.5px map-get($font-colors, primary);\n  }\n}\n\n// #l-header__server-banner {}\n// #l-header__channel-banner {}\n\n.l-header__server-img {\n  width: 30px;\n  height: 30px;\n  margin-right: $space-xsm;\n  border-radius: map-get($border-radius-size, primary);\n  object-fit: cover;\n  cursor: pointer;\n}\n\n.l-header__server-name { max-width: 350px; }\n\n.l-header__server-name, .l-header__channel-name {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n  @include mq(sp) {\n    max-width: 125px;\n  }\n}\n\n\n/* --------------------------------------------------------------------\n|  main menu                                                           |\n----------------------------------------------------------------------*/\n\n\n#l-header__mainmenu-toggler {\n  position: relative;\n}\n\n#l-header__mainmenu-opener {\n  transform: scale(1);\n  position: absolute;\n  top: -17px;\n  right: 0;\n  border: none;\n  font-size: map-get($font-size, btn-icon);\n  > i {\n    margin: auto 0;\n  }\n  &[data-status=\"on\"] {\n    transition: transform 0.15s 0s ease;\n    transform: scale(0);\n  }\n  &[data-status=\"off\"] {\n    transition: transform 0.15s 0.15s ease;\n    transform: scale(1);\n  }\n}\n\n#l-header__mainmenu-closer {\n  transform: scale(0);\n  position: absolute;\n  top: -17px;\n  right: 0;\n  &[data-status=\"on\"] {\n    transition: transform 0.15s 0.15s ease;\n    transform: scale(1);\n  }\n  &[data-status=\"off\"] {\n    transition: transform 0.15s 0s ease;\n    transform: scale(0);\n  }\n}\n\n#l-header__mainmenu {\n  transition: transform 0.3s 0s ease;\n  display: flex;\n  flex-flow: row;\n  align-items: center;\n  z-index: 4;\n  padding: 0 $space-xlg;\n  background-color: map-get($bg-colors, secondary);\n  @include mq(tab-max) {\n    flex-flow: column;\n    align-items: initial;\n    z-index: 3;\n    position: absolute;\n    bottom: 1.5px;\n    width: 100%;\n  }\n  &[data-status=\"on\"] {\n    transform: translateY(100%);\n  }\n  &[data-status=\"off\"] {\n    transform: translateY(-60px);\n  }\n}\n\n.l-header__mainmenu-item {\n  position: relative;\n  @include hover(pc) {\n    .l-header__submenu {\n      display: block;\n    }\n  }\n}\n\n.l-header__submenu {\n  display: none;\n  @include mq(pc) {\n    position: absolute;\n    top: 38px;\n    right: 0;\n    width: max-content;\n    min-width: 220px;\n    max-width: 250px;\n    padding: $space-2xsm;\n    border: map-get($border, primary);\n    border-radius: map-get($border-radius-size, primary);\n    background-color: map-get($bg-colors, secondary);\n  }\n  @include mq(tab-max) {\n    &[data-status=\"on\"] {\n      display: block;\n    }\n    &[data-status=\"off\"] {\n      display: none;\n    }\n  }\n}\n\n.l-header__submenu-toggler {\n  display: flex;\n  justify-content: space-between;\n  width: 100%;\n  cursor: pointer;\n}\n\n.l-header__submenu-arrow {\n  position: relative;\n  display: inline-block;\n  &::before{\n    content: '';\n    position: absolute;\n    top: 50%;\n    right: 0.3rem;\n    transform: rotate(135deg);\n    width: 7px;\n    height: 7px;\n    margin-top: -0.5rem;\n    border-right: solid 1.5px map-get($font-colors, primary);\n    border-top: solid 1.5px map-get($font-colors, primary);\n  }\n}\n\n.l-header__submenu-pilar-icon { //? pilar icon componentならん？ (p-index-aboutにあり)\n  height: 2rem;\n  border-left: 4px solid white;\n  margin: auto $space-md auto 0;\n}\n\n.l-header__submenu-item-title {\n  font-size: map-get($font-size, title-quaternary);\n}\n\n.l-header__submenu-item-detail {\n  font-size: map-get($font-size, note);\n  margin-top: $space-xsm;\n  word-wrap: break-word;\n}\n\n.l-header__flash-wrap {\n  display: flex;\n  position: fixed;\n  top: map-get($l-header, height);\n}\n\n.l-header__flash-message {\n  display: flex;\n  justify-content: space-between;\n  width: 100vw;\n  padding: $space-2xsm $space-xlg;\n  background-color: map-get($bg-colors, secondary);\n}","// _mixin.scss\n\n@use \"sass:meta\";\n\n// メディアクエリ ---------------------------------------------------------\n$breakpoints: (\n  sp:         'screen and (max-width: 767px)',\n  tab-max:    'screen and (max-width: 1024px)',\n  tab:        'screen and (min-width: 768px) and (max-width: 1024px)',\n  tab-min:    'screen and (min-width: 768px)',\n  pc:         'screen and (min-width: 1025px)'\n);\n\n@mixin mq($bp) {\n  @media #{map-get($breakpoints, $bp)} {\n    @content;\n  }\n}\n\n// 対象画面を絞るmin-width (al->allの意, spは指定なし)\n$frame-min-widths: (\n  al:    'min-width: 0px',\n  tab:   'min-width: 768px',\n  pc:    'min-width: 1025px'\n);\n\n// Hoverを設定 (対象デバイスがhover可能およびポインターデバイスを使用している場合に機能します)\n@mixin hover ($frame: null) {\n  $frame-min-width: \"\";\n  @if $frame == pc {\n    $frame-min-width: map-get($frame-min-widths, pc);\n  } @else if $frame == tab {\n    $frame-min-width: map-get($frame-min-widths, tab);\n  } @else {\n    $frame-min-width: map-get($frame-min-widths, al);\n  }\n  @media (hover: hover) and (pointer: fine) and ($frame-min-width) {\n    &:hover {\n      @content;\n    }\n  }\n}\n\n// Link(aタグ etc.)用のサイズ適正化\n@mixin optimized-link {\n  display: flex;\n  align-items: center;\n}","// _main.scss\n\n@use \"../foundation/tools/global\" as *;\n\n.l-main {\n  padding-top: map-get($l-header, height);\n}\n\n.l-main__section, .l-main__app-section {\n  display: flex;\n  min-height: calc(100vh - (map-get($l-header, height)));\n  width: 100vw;\n  > .content, .app-content {\n    height: fit-content;\n    width: map-get($l-main, content-width);\n    max-width: map-get($l-main, content-maxwidth);\n    padding: $space-xlg 0;\n  }\n  > .content {\n    margin: auto;\n    @include mq(tab-min) {\n      padding: $space-2xlg 0;\n    }\n  }\n  > .app-content {\n    margin: 0 auto;\n  }\n}\n\n.l-main__section:last-child {\n  min-height: calc(100vh - (map-get($l-header, height) + (map-get($l-footer, height))));\n}\n\n.l-main:nth-child(1) { // headerが存在しない場合に画角を調節する\n  padding: 0;\n  .l-main__section { min-height: calc(100vh - (map-get($l-footer, height))); }\n}\n\n.l-main__section-header {\n  text-align: center;\n  margin-bottom: $space-lg;\n  font-size: map-get($p-entry, header-font-size);\n  @include mq(sp) {\n    font-size: map-get($font-size, title-secondary);\n  }\n}\n\n.l-main__section-brief {\n  text-align: center;\n  margin-bottom: $space-xlg;\n  font-size: map-get($font-size, brieftxt-pc);\n  line-height: map-get($lineheight-size, double);\n  @include mq(sp) {\n    font-size: map-get($font-size, brieftxt-sm);\n  }\n}\n\n.l-main__link-section {\n  display: flex;\n  justify-content: space-between;\n  width: 100%;\n  margin-bottom: 1.5rem;\n  font-size: 1rem;\n}\n\n.l-main__link-section-text { margin: 0 $space-2xsm; }","// _footer.scss\n\n@use \"../foundation/tools/global\" as *;\n\n.l-footer {\n  height: map-get($l-footer, height);\n  padding: map-get($l-footer, padding);\n  line-height: map-get($l-footer, height);\n}\n\n.l-footer__copy-light-txt {\n  font-size: map-get($font-size, copy-right);\n}","// _button.scss\n\n@use \"../../foundation/tools/global\" as *;\n\n// ボタンのベースです\n.c-btn {\n  display: flex;\n  justify-content: space-around;\n  align-items: center;\n  padding: 0 $space-2xsm;\n  font-size: $font-size-md;\n  color: inherit;\n  cursor: pointer;\n  > i { // FontAwesome経由のiconを中央に\n    margin: auto;\n  }\n}\n\n// background-color付きのボタンです\n@each $key, $value in $btn-bg-colors {\n  .c-btn--bg-#{$key} {\n    @extend .c-btn;\n    background-color: $value;\n  }\n  .c-btn--hover-bg-#{$key}{\n    @extend .c-btn;\n    border: map-get($border, \"#{$key}\");\n    @include hover() {\n      background-color: $value;\n      @if $key == \"white-back\" {\n        color: map-get($font-colors, primary);\n      }\n    }\n  }\n}\n\n// 四角形のボタンです\n.c-btn--quad {\n  @extend .c-btn;\n  min-height: 34px;\n  min-width: 34px;\n  border-radius: map-get($border-radius-size, primary);\n}\n\n.c-btn--submit {\n  @extend .c-btn--quad;\n  padding: 0 4rem;\n}","// _wrapper.scss\n\n@use \"../../foundation/tools/global\" as *;\n\n.c-flex-wrapper { display: flex; }\n\n.c-flex-wrapper--center {\n  display: flex;\n  align-items: center;\n}\n\n.c-flex-wrapper--column {\n  display: flex;\n  flex-flow: column;\n}\n\n.c-flex-wrapper--row {\n  display: flex;\n  flex-flow: row;\n}","// _auth.scss //! No Use\n\n@use \"../../foundation/tools/global\" as *;\n\n.p-auth__form {\n  width: 50%;\n  min-width: 250px;\n  margin: 0 auto;\n}\n\n.p-auth__input-label { font-size: map-get($font-size, label); }\n\n.p-auth__input {\n  width: 100%;\n  margin: $space-2xsm 0 $space-lg;\n  padding: $space-sm;\n  border: map-get($border, primary);\n  border-radius: map-get($border-radius-size, primary);\n  font-size: map-get($font-size, input);\n  font-weight: 600;\n  background-color: map-get($bg-colors, primary);\n}\n\n.p-auth__error-msg {\n  float: right;\n  font-size: map-get($font-size, note);\n  color: map-get($font-colors, alert);\n}\n\n.p-auth__submit {\n  margin: 1.5rem auto 0;\n  border: none;\n}","// _auth.scss\n\n@use \"../../foundation/tools/global\" as *;\n\n.p-form__wrap {\n  width: 50%;\n  min-width: 250px;\n  margin: 0 auto;\n}\n\n.p-form__input-label, .p-form__image-label { font-size: map-get($font-size, label); }\n\n.p-form__image-label {\n  width: fit-content;\n  display: block;\n}\n\n.p-form__input {\n  width: 100%;\n  margin: $space-2xsm 0 $space-lg;\n  padding: $space-sm;\n  border: map-get($border, primary);\n  border-radius: map-get($border-radius-size, primary);\n  font-size: map-get($font-size, input);\n  font-weight: 600;\n  background-color: map-get($bg-colors, primary);\n}\n\n.p-form__error-msg {\n  float: right;\n  margin-left: $space-md;\n  font-size: map-get($font-size, note);\n  color: map-get($font-colors, alert);\n}\n\n.p-form__submit {\n  margin: $space-lg auto 0;\n  border: none;\n}\n\n#p-form__password-visualizer {\n  i:last-child { display: none; }\n}\n\n.p-form__checkbox-wrap {\n  display: flex;\n  justify-content: space-evenly;\n}\n\n.p-form__checkbox-label {\n  padding: $space-2xsm $space-sm;\n  border-radius: map-get($border-radius-size, primary);\n  background-color: map-get($btn-bg-colors, primary);\n}\n\n.p-form__checkbox { display: none; }\n\n.p-form__checkbox:checked + .p-form__checkbox-label {\n  background-color: map-get($btn-bg-colors, secondary);\n}\n\n.p-form__input:disabled {\n  text-decoration-line: line-through;\n  background-color: map-get($bg-colors, secondary);\n}\n\n.p-form__image-block {\n  background-color: #222;\n  width: 175px;\n  margin-top: $space-2xsm;\n  padding: $space-md $space-xsm;\n  border-radius: map-get($border-radius-size, primary);\n}\n\n.p-form__image-wrap {\n  display: flex;\n  position: relative;\n  margin-bottom: $space-md;\n  height: 160px;\n  border-radius: map-get($border-radius-size, primary);\n  font-size: map-get($font-size, btn-icon-exlg);\n  background-color: map-get($bg-colors, quinary);\n  > i {\n    position: absolute;\n    top: calc(50% - map-get($font-size, btn-icon-exlg) / 2);\n    left: calc(50% - map-get($font-size, btn-icon-exlg) / 2);\n  }\n  > img {\n    z-index: 5;\n    height: 100%;\n    width: 100%;\n    object-fit: cover;\n  }\n}\n\n.p-form__filename { font-size: map-get($font-size, primary); }","// _modal.scss\n\n@use \"../../foundation/tools/global\" as *;\n\n.p-modal {\n  z-index: 7;\n  opacity: 0;\n  transform: scaleY(0);\n  transition: transform 0.2s ease-out, opacity 0.08s ease-in;\n  position: fixed;\n  top: map-get($p-modal, modal-top-pos);\n  right: 0;\n  left: 0;\n  width: 85%;\n  margin: auto;\n  padding: 0;\n  border-radius: map-get($border-radius-size, primary);\n  background-color: map-get($bg-colors, modal);\n  &[data-status=\"on\"] {\n    opacity: 1;\n    transform: scaleY(1);\n  }\n  &[data-status=\"off\"] {\n    opacity: 0;\n    transform: scaleY(0);\n  }\n}\n\n.p-modal-closer {\n  position: absolute;\n  top: 1%;\n  right: 1%;\n}\n\n.p-modal-wrap{\n  overflow: scroll;\n  max-height: 86vh;\n  padding: 4% 1%;\n  // border-radius: map-get($border-radius-size, primary);\n  // background-color: map-get($bg-colors, entry-modal);\n}\n\n.p-modal-content{ margin: 0; }\n\n.p-modal-block { padding: $space-sm; }\n\n.p-modal-header {\n  text-align: center;\n  font-size: map-get($font-size, title-primary);\n  margin-bottom: $space-md;\n}","// _management.scss\n\n@use \"../../foundation/tools/global\" as *;\n\n.p-management__section-brief { font-size: map-get($font-size, title-tertiary); }\n\n.p-management__code {\n  background: map-get($bg-colors, secondary);\n  border-radius: map-get($border-radius-size, primary);\n  padding: 0 $space-2xsm;\n}\n\n.p-management__notice-wrap {\n  width: fit-content;\n  margin: 0 auto;\n  padding: $space-md $space-lg;\n  border-left: map-get($border, notice-card);\n  border-radius: map-get($border-radius-size, primary);\n  font-size: map-get($font-size, card);\n  line-height: map-get($lineheight-size, double);\n  background-color: map-get($bg-colors, secondary);\n}","// _shared.scss\n\n@use \"../../foundation/tools/global\" as *;\n\n// モーダル起動時用のスタイルです ----------------------------------------\n#body {\n  &[data-status=\"on\"] {\n    overflow: hidden;\n  }\n  &[data-status=\"off\"] {\n    overflow: visible;\n  }\n}\n\n#modal-overlay {\n  display: none;\n  opacity: 0.9;\n  position: fixed;\n  top: 0;\n  z-index: 6;\n  height: 100%;\n  width: 100%;\n  background-color: $black;\n  &[data-status=\"on\"] {\n    display: block;\n  }\n  &[data-status=\"off\"] {\n    display: none;\n  }\n}\n\n#loading {\n  position: fixed;\n  top: 0;\n  left: 0;\n  z-index: 7;\n  width: 100vw;\n  height: 100vh;\n  transition: all 1s;\n  background-color: map-get($bg-colors, primary);\n}\n\n.loader,\n.loader:before,\n.loader:after {\n  background: #ffffff;\n  -webkit-animation: load1 1s infinite ease-in-out;\n  animation: load1 1s infinite ease-in-out;\n  width: 1em;\n  height: 4em;\n}\n.loader {\n  color: #ffffff;\n  text-indent: -9999em;\n  margin: calc(40vh) auto;\n  position: relative;\n  font-size: 11px;\n  -webkit-transform: translateZ(0);\n  -ms-transform: translateZ(0);\n  transform: translateZ(0);\n  -webkit-animation-delay: -0.16s;\n  animation-delay: -0.16s;\n}\n.loader:before,\n.loader:after {\n  position: absolute;\n  top: 0;\n  content: '';\n}\n.loader:before {\n  left: -1.5em;\n  -webkit-animation-delay: -0.32s;\n  animation-delay: -0.32s;\n}\n.loader:after {\n  left: 1.5em;\n}\n@-webkit-keyframes load1 {\n  0%,\n  80%,\n  100% {\n    box-shadow: 0 0;\n    height: 4em;\n  }\n  40% {\n    box-shadow: 0 -2em;\n    height: 5em;\n  }\n}\n@keyframes load1 {\n  0%,\n  80%,\n  100% {\n    box-shadow: 0 0;\n    height: 4em;\n  }\n  40% {\n    box-shadow: 0 -2em;\n    height: 5em;\n  }\n}\n\n.loaded {\n  opacity: 0;\n  visibility: hidden;\n}","// _user.scss\n\n@use \"../../../foundation/tools/global\" as *;\n\n// User List ---------------------------------------------\n// #p-user-list { }\n\n// .p-user-list__table {}\n\n.p-user-list__head-wrap {\n  text-align: center;\n  margin-bottom: $space-xsm;\n}\n\n.p-user-list__head {\n  font-weight: bold;\n  font-size: map-get($font-size, table-label-primary);\n}\n\n.p-user-list__user-info-list {\n  align-items: center;\n  margin-bottom: $space-xsm;\n  border-radius: map-get($border-radius-size, primary);\n  background-color: map-get($bg-colors, secondary)\n}\n\n.p-user-list__user-info-wrap {\n  display: flex;\n  padding: $space-2xsm $space-sm;\n  @include mq(sp) {\n    padding: 0 $space-sm $space-2xsm ;\n    &:first-child {\n      padding-top: $space-2xsm;\n    }\n  }\n}\n\n.p-user-list__user-info { margin: 0 auto; }\n\n.p-user-list__user-info--name {\n  margin: 0 auto;\n  @include hover() {\n    text-decoration-line: underline;\n  }\n}\n\n.p-user-list__user-delete-btn {\n  margin: 0 auto;\n  padding: $space-2xsm $space-sm;\n  border-radius: map-get($border-radius-size, primary);\n  background-color: map-get($btn-bg-colors, white-back);\n}","@use \"../../../foundation/tools/global\" as *;\n\n\n// Server --------------------------------------------------------\n.p-index__server-board {\n  margin-bottom: $space-lg;\n  padding: 0 $space-md;\n}\n\n.p-index__server-thumnail-wrap {\n  transition: scale 0.2s ease;\n  overflow: hidden;\n  position: relative;\n  margin-bottom: $space-2xsm;\n  padding-top: 95%;\n  border-radius: map-get($border-radius-size, p-secondary);\n  @include hover() {\n    scale: 1.05;\n    > .p-index__server-edit-btn {\n      display: block;\n    }\n  }\n}\n\n.p-index__server-thumnail {\n  display: flex;\n  justify-content: space-around;\n  align-items: center;\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  background-color: map-get($bg-colors, tertiary);\n  > i {\n    font-size: map-get($font-size, btn-icon-lg);\n  }\n  > img {\n    height: 100%;\n    width: 100%;\n    object-fit: cover;\n  }\n}\n\n.p-index__server-edit-btn {\n  display: none;\n  position: absolute;\n  top: -0.5rem;\n  right: -0.3rem;\n  padding: 1rem 1rem 0.5rem 0.7rem;\n  border-radius: 0.5rem;\n  background-color: map-get($bg-colors, secondary);\n  @include mq(tab-max) {\n    display: block;\n  }\n}\n\n\n.p-index__server-title {\n  text-align: center;\n  font-size: map-get($font-size, title-tertiary);\n}\n\n\n\n\n// main --------------------------------------------------------\n\n.p-index__sidebar {\n  position: fixed;\n  height: 100%;\n  width: map-get($p-index-sidebar, width);\n  padding: $space-xsm;\n  border-top: map-get($border, white-back);\n  background-color: map-get($bg-colors, secondary);\n}\n// .p-index__sidebar__channel-list-wrap {}\n.p-index__sidebar__channel-header {\n  display: flex;\n  justify-content: space-between;\n  width: 100%;\n  padding: $space-xsm;\n}\n// .p-index__sidebar__channel-new-btn {}\n// .p-index__sidebar__channel-list {}\n.p-index__sidebar__channel-wrap {\n  margin-bottom: $space-2xsm;\n  padding: $space-xsm $space-2xsm;\n  border-radius: map-get($border-radius-size, primary);\n  cursor: pointer;\n  @include hover {\n    background-color: map-get($bg-colors, tertiary);\n  }\n}\n.p-index__sidebar__hash-mark {\n  text-align: center;\n  font-size: map-get($font-size, btn-icon-md);\n}\n.p-index__sidebar__channel-name {\n  font-size: map-get($font-size, title-tertiary);\n}\n.p-index__sidebar__invite-btn, .p-index__sidebar__setting-btn {\n  text-align: center;\n  font-size: map-get($font-size, btn-icon-sm);\n  > i {\n    transition: all 0.15s;\n    vertical-align: middle;\n    @include hover {\n      scale: 1.4;\n    }\n  }\n}\n\n\n\n.p-index-main {\n  width: calc(100% - map-get($p-index-sidebar, width));\n  margin-left: 245px;\n  @include mq(sp) {\n    width: 100%;\n    margin: 0;\n  }\n}\n\n.p-index-main__output {}\n\n.p-index-main__input {}","//_utility.scss\n\n@use \"../../foundation/tools/global\" as *;\n\n// 該当のデバイス画角に対応するdisplay:noneです -------------------------------------\n\n//Smartphone(sp)\n.u-dn-sp {\n  @include mq(sp) {\n    display: none !important;\n  }\n}\n\n//Tablet(tab)\n.u-dn-tab-max {\n  @include mq(tab-max) {\n    display: none !important;\n  }\n}\n\n.u-dn-tab-min {\n  @include mq(tab-min) {\n    display: none !important;\n  }\n}\n\n// PC(pc)\n.u-dn-pc {\n  @include mq(pc) {\n    display: none !important;\n  }\n}\n\n.u-dn {\n  display: none !important;\n}\n\n\n// 該当のデバイス画角に対応してアニメーションを無効にします -----------------------------\n\n//Smartphone(sp)\n.u-non-anim-sp {\n  @include mq(sp) {