// file.py

const FILE_ELM = get_element("#image")
const FILENAME_ELM = get_element(".p-form__filename")[0]
const IMAGE_ERROR_MSG = get_element("#p-form__image-error-msg")

const IMAGE_WRAP_ELM_NAME = ".p-form__image-wrap"
const IMAGE_ELM = get_element(IMAGE_WRAP_ELM_NAME)[0]

const ALLOWED_IMAGE_FORMAT = ["jpg", "jpeg", "png"]


validate_image = (filename) => {
    format = filename.split('.').slice(-1)[0].toLowerCase();

    if (ALLOWED_IMAGE_FORMAT.includes(format)) {
        IMAGE_ERROR_MSG.textContent = "";
        return true;
    } 
    else {
        IMAGE_ERROR_MSG.textContent = "jpg / png 形式の画像のみ登録いただけます。";
        return false;
    }
}

update_image = (file) => {
    let file_reader = new FileReader();
    // ロードが終わるとonloadに設定した関数が実行(event : ProgressEvent オブジェクト)
    file_reader.onload = (event) => {
        // event.currentTarget.resultにbase64文字列が入る
        let base64_text = event.currentTarget.result
        let img = document.createElement("img");
        img.src = base64_text;

        // 指定した要素の中の末尾に挿入
        IMAGE_ELM.appendChild(img);
    }
    file_reader.readAsDataURL(file)
}

clear_image = () => {
    img = get_element(`${IMAGE_WRAP_ELM_NAME} img`)[0]
    if (img) img.remove();
}

update_image_form = () => {
    if (FILE_ELM && FILENAME_ELM && IMAGE_ELM) {
        FILE_ELM.addEventListener('change', () => {
            clear_image();
            if (FILE_ELM.files[0] && validate_image(FILE_ELM.files[0].name)) {
                let file = FILE_ELM.files[0];
                FILENAME_ELM.textContent = file.name;
                update_image(file);
            } else {
                FILENAME_ELM.textContent = "No file chosen";
            }
        })
    }
}


update_image_form();